import React from 'react';
import Login from './Login.js';
import Update from './Update.js';

/**
*   This class handles login and update functionality by using the API endpoints login and update-title
*   @author Mario Nemecek
*/

class Admin extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            authenticated: false,
            token: "",
            email:"", 
            password:"",
            errorMsg: ""
        }
        
        this.handleEmail = this.handleEmail.bind(this);
        this.handlePassword = this.handlePassword.bind(this);
    }
    

    postData = (url, myJSON, callback) => {
        fetch(url, {
            method : 'POST',
            headers : new Headers(),
            body:JSON.stringify(myJSON)
        })
        .then( (response) => response.json())
        .then( (data) => {
            callback(data)
        })
        .catch( (err) => {
            console.log('Something went wrong:', err)
        });
    }
    
    loginCallback = (data) => {
        console.log(data)
        if(data.status === 401) {
            this.setState({errorMsg:data.message})
        }
        if(data.status === 200) {
            this.setState({authenticated:true, token:data.token})
            localStorage.setItem('myToken', data.token);  
            localStorage.setItem('admin', data.admin);  
        }
    } 
    
    updateCallback = (data) => {
        console.log(data)
        if(data.status !== 200) {
            this.setState({authenticated:false})
            localStorage.removeItem('myToken', data.token);  
        }
        
    }

    handleLoginClick = () => {
        const url = 'http://unn-w17015200.newnumyspace.co.uk/kf6012/assessment/part1/api/login/';
        let myJSON = {
            'email':this.state.email,
            'password':this.state.password
        }
        this.postData(url, myJSON, this.loginCallback);
    }
    
    handleLogoutClick = () => {
        this.setState({authenticated:false, errorMsg:"", email:"", password:""})
        localStorage.removeItem('myToken');
        localStorage.removeItem('admin');
    }
    
    handlePassword = (e) => {
        this.setState({password:e.target.value})
    }
    
    handleEmail = (e) => {
        this.setState({email:e.target.value})
    }
    
    handleUpdateClick = (sessionId, title) => {
        const url = 'http://unn-w17015200.newnumyspace.co.uk/kf6012/assessment/part1/api/update-title/';
        
        if(localStorage.getItem('myToken')) {
            let myToken = localStorage.getItem('myToken');
            let myJSON = 
                {
                    'title' : title,
                    'token' : myToken,
                    'sessionId' : sessionId
                }
            this.postData(url, myJSON, this.updateCallback);
        } else {
            this.setState({authenticated:false})
        }   
    }
    
    componentDidMount() {
        if(localStorage.getItem('myToken')) {
            this.setState({authenticated:true})
        }
    }

    render() {
        let page = <Login errorMsg={this.state.errorMsg} handleLoginClick={this.handleLoginClick} email={this.state.email} password={this.props.password} handleEmail={this.handleEmail} handlePassword={this.handlePassword}/>
              
        if(this.state.authenticated) {
             page = 
                 <>
                 <button className="logout" onClick={this.handleLogoutClick}>Log out</button>
                 <Update handleUpdateClick={this.handleUpdateClick} admin={this.state.admin} searchDetails={this.searchDetails}/>
                 </>
        }
        
        return (
            <div className="pageContainer">
                <h1>Admin</h1>
                {page}
            </div>
        )
    }
}

export default Admin;
