import React from 'react';

/**
*   Displays the title of each session and allows an admin user to update the session title by changing
*   the string in the textarea
*   @author Mario Nemecek
*/

class UpdateItem extends React.Component {
    
    state = {
        title: this.props.details.title
    }

    handleTitleChange = (e) => {
        if(this.props.admin) {
            this.setState({title:e.target.value})
        }
    }
    
    handleTitleUpdate = () => {
        this.props.handleUpdateClick(this.props.details.sessionId, this.state.title)   
    }
    
    render() {
        let updateButton = <p>Sorry, only administrators can update sessions.</p>
        
        if(localStorage.getItem('admin') === "true") {
            updateButton = <button onClick={this.handleTitleUpdate}>Update</button>;
        }
        
        return (
            <div className="wrapper">
                <h3>Title: {this.props.details.title}</h3>
                <div className="update">
                    <textarea 
                        value={this.state.title} 
                        onChange={this.handleTitleChange}
                    />
                    <br></br>
                    {updateButton}
                </div>
            </div>
        )
    }
}

export default UpdateItem;