import React from 'react';
import {BrowserRouter as Router, Switch, Route, NavLink} from 'react-router-dom';

import Sessions from './components/Sessions.js';
import Authors from './components/Authors.js';
import Admin from './components/Admin.js';
import NotFound404 from './components/NotFound404.js';

import './App.css';

function App() {
    return (
        <Router basename="/kf6012/assessment/part2">
            <div className="App">
                <nav>
                    <ul>
                        <li>
                            <NavLink activeClassName="selected" exact to="/">Schedule</NavLink>
                        </li>
                        <li>
                            <NavLink activeClassName="selected" to="/authors">Authors</NavLink>
                        </li>
                         <li>
                            <NavLink activeClassName="selected" to="/admin">Admin</NavLink>
                        </li>
                    </ul>
                </nav>
                
                <Switch>
                    <Route path="/authors">
                        <Authors />
                    </Route>
                    <Route path="/admin">
                        <Admin />
                    </Route>
                    <Route exact path="/">
                        <Sessions />
                    </Route>
                    <Route path="*">
                        <NotFound404 />
                    </Route>
                </Switch>
            </div>
        </Router>
    );
}

export default App;
