import React from 'react';

/**
*   A slot dropdown that is dynamically updated from the API.
*   @author Mario
*/

class SelectSlot extends React.Component {
    
    constructor(props) {
        super(props);
        
        this.state = {
            data: [],
            days: ['Monday', 'Tuesday', 'Wednesday', 'Thursday']
        }
    }
    
    filterDay = (day) => {
        return (this.state.days.includes(day))
    }
    
    selectDetailsDay = (details) => {
        return(this.filterDay(details.day))
    }
    
    componentDidMount() {
        const url = 'http://unn-w17015200.newnumyspace.co.uk/kf6012/assessment/part1/api/slots/'
        
        fetch(url)
            .then((response) => response.json())
            .then((data) => {
                this.setState({data:data.data})
            })
            .catch((err) => {
                console.log('Something went wrong, ', err)
            });
    }
    
    render() {
        
        let filteredData = (this.state.data.filter(this.selectDetailsDay))
        
        return(
            <div className="slotDropdown">
                <label>
                Session Slots:
                    <select value={this.props.type} onChange={this.props.handleSelectSlot}>
                        <option value="">All Slots</option>
                        {filteredData.map( (details,i) => (<option key={i} value={details.id}>{details.day} {details.startTime}-{details.endTime}</option>) )}
                    </select>    
                </label>
            </div>
        )
    }
}

export default SelectSlot;