import React from 'react';

/**
*   Displays the authors name which can be clicked on to expand presentation details.
*   @author Mario Nemecek
*/

class Author extends React.Component {
    
    constructor(props) {
        super(props);
        
         this.state = {
            displayPresentations : false,
            author: "",
            data: []
         }
    }
   

    handleAuthorClick = () => {
        this.setState({
            displayPresentations:!this.state.displayPresentations,
        })
        this.getData(this.state.author)
    }
    
    componentDidMount() {
        this.setState({
            author: this.props.details.name
        })
    }
    
    componentDidUpdate() {       
        if(this.props.details.name !== this.state.author) {
            this.setState({
                displayPresentations: false,
                author: this.props.details.name,
                data: []
            })
        }
    }

    getData = (name) => {
        const url = 'http://unn-w17015200.newnumyspace.co.uk/kf6012/assessment/part1/api/content?author-name=' + name
        
        fetch(url)
            .then((response) => response.json())
            .then((data) => {
                this.setState({data:data.data})
            })
            .catch((err) => {
                console.log('Something went wrong, ', err)
            });
    }
    
    render() {
        let presentations = [];
        
        if(this.state.displayPresentations) {
            if(this.state.data) {
                this.state.data.forEach(function(content) {
                    presentations.push(
                        <div key={content.id}>
                            <hr></hr>
                            <p><strong>Title:</strong> {content.title}</p>
                        </div>
                    )
                });
            }
        }
        
        return (
            <div className="wrapper">
                <h3 className="authorName" onClick={this.handleAuthorClick}>{this.props.details.name}</h3>
                {presentations}
            </div>
        )
    }
}

export default Author;