import React from 'react';

/**
*   Displays the session title which can be clicked to expand further details.
*   @author Mario Nemecek
*/

class Session extends React.Component {
    
    state = {
        displaySessionData : false,
        displayContentData : false,
        id: "",
        data: []
    }

    handleSessionTitleClick = () => {
        this.setState({displaySessionData:!this.state.displaySessionData})
        if(this.state.displayContentData === true) {
            this.setState({displayContentData:!this.state.displayContentData})
        }
    }
    
    handleContentClick = () => {
        this.setState({displayContentData:!this.state.displayContentData})
        this.getData(this.state.id)
    }
    
    componentDidMount() {
        this.setState({
            id: this.props.details.sessionId
        })
    }

    componentDidUpdate() {
        if(this.props.details.sessionId !== this.state.id) {
            this.setState({
                displaySessionData : false,
                displayContentData : false,
                id: this.props.details.sessionId,
                data: []
            })   
        }
    }
    
    getData = (id) => {  
        const url = 'http://unn-w17015200.newnumyspace.co.uk/kf6012/assessment/part1/api/content?session=' + id
        
        fetch(url)
            .then((response) => response.json())
            .then((data) => {
                this.setState({data:data.data})
            })
            .catch((err) => {
                console.log('Something went wrong, ', err)
            });
    }
    
    render() {
        let sessionData = "";
        let contentData = [];
        
        if(this.state.displaySessionData) {
            sessionData = (
                <div className="sessionData">
                    <div className="sessionDetails">
                        <span><strong>Type:</strong> {this.props.details.sessionType}</span>
                        <span><strong>Session Chair:</strong> {this.props.details.sessionChair}</span>
                        <span><strong>Room:</strong> {this.props.details.room}</span>
                    </div>
                    <button onClick={this.handleContentClick}>Check For Presentations</button>
                </div>
            )
        }
                                        
        if(this.state.displayContentData) {
            if(this.state.data) {
                this.state.data.forEach(function(content) {
                    contentData.push(
                        <div key={content.id}>
                            <hr></hr>
                            <p><strong>Title:</strong> {content.title}</p>
                            <p><strong>Authors:</strong> {content.authors}</p>
                            <p><strong>Awards:</strong> {content.award}</p>
                        </div>
                    )
                });
            }
        }
        
        return (
            <div className="wrapper">
                <h1 className="sessionTitle" onClick={this.handleSessionTitleClick}>{this.props.details.title}</h1>
                {sessionData}
                {contentData}
            </div>
        )
    }
}

export default Session;