import React from 'react';
import Session from './Session.js';

import Search from './Search.js';
import SelectSlot from './SelectSlot.js';
import SelectSessionType from './SelectSessionType.js';

/**
*   Fetches all the sessions from the API and can be filtered by name (search), session type and slot.
*   @author Mario Nemecek
*/

class Sessions extends React.Component {
    
    constructor(props) {
        super(props);
        
        this.state = {
            page:1,
            pageSize:9,
            query:"",
            type:"",
            slot:"",
            data : []
        }
        
        this.handleSearch = this.handleSearch.bind(this);
        this.handleSelectType = this.handleSelectType.bind(this);
        this.handleSelectSlot = this.handleSelectSlot.bind(this);
    }


    handleMoreClick = () => {
        this.setState({page:this.state.page+1})
    }
    
    handleSearch = (e) => {
        this.setState({page:1, query:e.target.value})
    }
    
    handleSelectSlot = (e) => {
        this.setState({page:1, slot:e.target.value})
    }
    
    handleSelectType = (e) => {
        this.setState({page:1, type:e.target.value})
    }
    
    handleNextClick = () => {
        this.setState({page:this.state.page+1})
    }
 
    handlePreviousClick = () => {
        this.setState({page:this.state.page-1})
    }
    
    searchString = (s) => {
        return s.toLowerCase().includes(this.state.query.toLowerCase())
    }
    
    searchDetails = (details) => {
        return ((this.searchString(details.title)))
    }
    
    selectDetailsType = (details) => {
        return ((this.state.type === details.sessionType) || (this.state.type === ""))
    }
    
    selectDetailsSlot = (details) => {
        return ((this.state.slot === details.slotId) || (this.state.slot === ""))
    }
    
    componentDidMount() {
        const url = 'http://unn-w17015200.newnumyspace.co.uk/kf6012/assessment/part1/api/sessions/'
        
        fetch(url)
            .then((response) => response.json())
            .then((data) => {
                this.setState({data:data.data})
            })
            .catch((err) => {
                console.log('Something went wrong, ', err)
            });
    }
    
    
    render() {
        let filteredData = (
                this.state.data
                .filter(this.selectDetailsSlot)
                .filter(this.selectDetailsType)
                .filter(this.searchDetails)
        )
        
        let noOfPages = Math.ceil(filteredData.length/this.state.pageSize)
        if(noOfPages === 0) {noOfPages = 1}
        
        let disabledPrevious = (this.state.page <= 1)
        let disabledNext = (this.state.page >= filteredData.length/this.state.pageSize)
        
        return (
            <div className="pageContainer">
                <h1>Conference Schedule</h1>
                
                <p>Click on the title of the session to toggle information such as type of session, session chair and room number. You can also check if there are any presentations for that session by clicking on the "check for presentation" button.</p>
                
                <SelectSlot value={this.state.slot} handleSelectSlot={this.handleSelectSlot}/>
                <SelectSessionType value={this.state.type} handleSelectType={this.handleSelectType}/>
                <Search query={this.state.query} handleSearch={this.handleSearch}/>
                
                <div className="sessions">
                    {filteredData
                        .slice((this.state.page*this.state.pageSize)-this.state.pageSize,(this.state.page*this.state.pageSize))
                        .map( (details, i) => (<Session key={i} details={details}/>) )}
                </div>
                
                <div className="pageButton">
                    <button onClick={this.handlePreviousClick} disabled={disabledPrevious}>Previous</button>
                    Page {this.state.page} of {noOfPages}
                    <button onClick={this.handleNextClick} disabled={disabledNext}>Next</button>
                </div>
            </div>
        )
    }
}

export default Sessions;              