import React from 'react';

/**
*   A session type dropdown that is dynamically updated from the API.
*   @author Mario Nemecek
*/

class SelectSessionType extends React.Component {
    
    constructor(props) {
        super(props);
        
        this.state = {data:[]}
    }
    
    componentDidMount() {
        const url = 'http://unn-w17015200.newnumyspace.co.uk/kf6012/assessment/part1/api/sessions?type=all'
        
        fetch(url)
            .then((response) => response.json())
            .then((data) => {
                this.setState({data:data.data})
            })
            .catch((err) => {
                console.log('Something went wrong, ', err)
            });
    }
    
    render() {
        return(
            <div className="sessionTypeDropdown">
                <label>
                Session Type:
                    <select value={this.props.type} onChange={this.props.handleSelectType}>
                        <option value="">Any</option>
                        {this.state.data.map( (details,i) => (<option key={i} value={details.sessionType}>{details.name}</option>) )}
                    </select>    
                </label>
            </div>
        )
    }
}

export default SelectSessionType;