import React from 'react';
import UpdateItem from './UpdateItem.js';

/**
*   Fetches all the sessions from the API and passes this data to the updateItem class as properties
*   @author Mario Nemecek
*/

class Update extends React.Component {
    
    state = {
        data:[]
    }

    
    componentDidMount() {
        const url = 'http://unn-w17015200.newnumyspace.co.uk/kf6012/assessment/part1/api/sessions/';
        
        fetch(url)
        .then( (response) => response.json())
        .then( (data) => {
            this.setState({data:data.data})
        })
        .catch( (err) => {
            console.log("Something went wrong", err)
        });
    }

    render() {        
        return (
            <div>
                {this.state.data.map((details,i) => (<UpdateItem key={i} details={details} handleUpdateClick={this.props.handleUpdateClick}/>))}
            </div>
        );
    }
}

export default Update;